.basket {
  padding: 30px 0 50px 0;


  .main {
    margin-bottom: 10px;
  }

  .header {
    margin-bottom: 30px;
  }

  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 18px;
    background-color: #fff;
    position: sticky;
    padding-bottom: 30px;
    bottom: 0;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;

    .sumBasket {
      font-weight: 700;
    }

    .count {
      font-weight: 700;
    }

    .footerPrice {
      display: flex;
      flex-direction: column;
    }

  }
}

.basketEmpty {
  padding-top: 40px;
  font-size: 18px;

  .title {
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}


@media screen and (min-width: 0) and (max-width: 767px) {
  .basket {
    .footer {
      flex-direction: column;
      align-items: end;
      .priceTitle{
        margin-bottom: 30px;
      }
    }
  }

}


